<template>
  <el-container>
    <el-header >
      <el-button @click="gotoAdminLogin" icon="el-icon-d-arrow-right" style="float: right;border: none" ></el-button>
    </el-header>
    <el-main>
      <div class="loginContainer">
        <el-form ref="loginForm" :rules="rules" :model="loginForm" label-width="80px">
          <h3 class="loginTitle">SubtleChat~</h3>
          <el-form-item label=":" prop="username">
            <el-input type="text" v-model="loginForm.username" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" prop="password">
            <el-input type="password"  v-model="loginForm.password" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" prop="code">
            <el-input type="text" @keydown.enter.native="submitLogin" v-model="loginForm.code" auto-complete="off" placeholder="" style="width:150px;"></el-input>
            <img :src="verifyCode" title="" @click="changeverifyCode" />
          </el-form-item>
          <el-checkbox v-model="checked" class="loginRemember"></el-checkbox><span> </span>
          <div>
            <el-button @click="showRegistryDialog" style="width:45% ;margin-right: 15px"></el-button>
            <el-button type="primary" style="width:45% ;" @click="submitLogin"  v-loading.fullscreen.lock="fullscreenLoading"></el-button>
          </div>
        </el-form>
      </div>
    </el-main>
    <el-dialog title="" :before-close="closeRegisterDialog" :visible.sync="registerDialogVisible" width="30%">
      <el-form :model="registerForm" status-icon :rules="registerRules" ref="registerForm" >
        <el-form-item label="" :label-width="formLabelWidth" prop="nickname">
          <el-input v-model=" registerForm.nickname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="username">
            <el-input v-model="registerForm.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="password">
          <el-input type="password" v-model="registerForm.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="checkPass">
          <el-input type="password" v-model="registerForm.checkPass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-upload
                  action="/ossFileUpload?module=group-chat"
                  ref="upload"
                  list-type="picture-card"
                  :class="{disabled:uploadDisabled}"
                  :before-upload="beforeAvatarUpload"
                  :file-list="fileList"
                  :on-progress="onProgress"
                  :on-success="imgSuccess"
                  :on-error="imgError"
                  :on-remove="imgRemove"
                  >
            <i  class="el-icon-plus"></i>
            <div slot="tip" class="el-upload__tip">4MB()</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitRegisterForm('registerForm') " style="width: 100%"></el-button>
      </div>
    </el-dialog>
  </el-container>


</template>

<script>

  export default {
    name: "Login",
    data(){
      var validateNickname = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        }
        //
          this.getRequest("user/checkNickname?nickname="+value).then(resp=>{
            if (resp!=0){
              callback(new Error(""))
            } else {
              callback();
            }
          })
      };
      var validateUsername = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        }
        //
        this.getRequest("/user/checkUsername?username="+value).then(resp=>{
            if (resp!=0){
              callback(new Error(''));
            }
            else {
              callback();
            }
          })

      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.registerForm.checkPass !== '') {
            this.$refs.registerForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.registerForm.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return{
        loginForm:{
           username:'',
           password:'',
           code:''
        },
        verifyCode:'/verifyCode',
        checked:true,
        rules: {
          username:[{required:true,message:'',trigger:'blur'}],
          password:[{required:true,message: '',trigger:'blur'}],
          code:[{required:true,message: '',trigger:'blur'}]
        },
        fullscreenLoading:false,
        //
        registerDialogVisible:false,
        formLabelWidth: '120px',
        registerForm:{
          nickname:'',
          username:'',
          password:'',
          checkPass:'',
          userProfile:'default_head.jpg',
        },
        registerRules: {
          nickname: [
            { validator: validateNickname, trigger: 'blur' }
          ],
          username: [
            { validator: validateUsername, trigger: 'blur' }
          ],
          password: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
        },
        uploadDisabled:false,
        //
        fileList:[],
      };
    },
    methods:{
      submitLogin(){
        this.$refs.loginForm.validate((valid) => {
          if (valid) {
            this.fullscreenLoading=true;
            this.postKeyValueRequest('/doLogin',this.loginForm).then(resp=>{
              setTimeout(()=>{
                this.fullscreenLoading=false;
              },1000);
              if (resp){
                //vuex
                this.$store.state.currentUser=resp.obj;
                //sessionStorage
                window.sessionStorage.setItem("user",JSON.stringify(resp.obj));
                let path=this.$route.query.redirect;
                this.$router.replace((path=='/'||path==undefined)?"/chatroom":path);
              }else {
                this.changeverifyCode();
              }
            })
          } else {
            this.$message.error(",");
            return false;
          }
        });
      },
      changeverifyCode(){
        this.verifyCode="/verifyCode?time="+new Date();
      },
      gotoAdminLogin(){
        this.$router.replace("/adminlogin");
      },
      showRegistryDialog(){
        this.registerDialogVisible=true;
      },
      /**
       *       
       */
      //
      beforeAvatarUpload(file) {
        let isLt4M = file.size / 1024 / 1024 < 4;

        if (!isLt4M) {
          this.$message.error(' 4MB!');
        }
        return isLt4M;
      },
      // 
      onProgress(event, file, fileList){
        this.uploadDisabled = true;
      },
      // 
      imgSuccess(response, file, fileList) {
        this.uploadDisabled = true;
        this.registerForm.userProfile=response;//
        console.log("url"+this.registerForm.userProfile);
      },
      // 
      imgError(err, file, fileList){
        this.$message.error("");
        this.uploadDisabled = false;
      },
      //
      imgRemove(file,fileList){
        this.uploadDisabled = false;
      },
      closeRegisterDialog(done){
        this.registerForm={//
          nickname:'',
          username:'',
          password:'',
          checkPass:'',
          userProfile:'',
        };
        //this.$refs.upload.clearFiles();//
        done();//
      },
      //
      submitRegisterForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.postRequest("/user/register",this.registerForm).then(resp=>{
              if (resp){
                this.registerDialogVisible=false;
                location.reload();//
              }
            })
          } else {
            this.$message.error("");
            console.log('error submit!!');
            return false;
          }
        });
      },

    }
  }
</script>


<style>
  .disabled .el-upload--picture-card{
    display: none;
  }
  .loginContainer{
    width: 350px;
    margin: 100px auto;
    border-radius:15px ;
    border: 1px solid #eaeaea;
    /* h-shadow()v-shadow()blur()color()*/
    box-shadow: 10px 10px 35px #cac6c6;
    background: #fff;
    /*background-clip*/
    /*border-box*/
    /*padding-box*/
    /*content-box*/
    background-clip: padding-box;
    padding: 25px 35px 25px 35px;
  }
  .loginTitle{
    margin: 10px auto 30px auto;
    text-align: center;
    color:#505458;
  }
  .loginRemember{
    margin: 5px auto 35px 80px;
  }
  /*.el-form-item__content{*/
  /* display: flex;*/
  /*  align-items: center*/
  /*}*/
</style>
