<template>
  <el-container>
    <el-header>
      <el-button @click="gotoClientLogin" icon="el-icon-d-arrow-left" style="border: none" ></el-button>
    </el-header>
    <el-main>
      <div class="loginContainer">
        <el-form ref="loginForm" :rules="rules" :model="loginForm" label-width="80px">
          <h3 class="loginTitle"></h3>
          <el-form-item label=":" prop="username">
            <el-input type="text" v-model="loginForm.username" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" prop="password">
            <el-input type="password"  v-model="loginForm.password" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" prop="mailCode">
<!--             <el-input type="text"  v-model="loginForm.mailCode" auto-complete="off" placeholder="" style="width: 120px;margin-right: 10px" ></el-input>           -->
            <el-input type="text"  v-model="loginForm.mailCode" auto-complete="off" placeholder="" style="width: 120px;margin-right: 10px" ></el-input>
             <el-button @click="getMailVerifyCode" :disabled="getCodeEnable"  size="mini">{{getCodeBtnText}}</el-button>
          </el-form-item>
          <el-checkbox v-model="checked" class="loginRemember"></el-checkbox><span> </span>
          <el-button type="primary" style="width:100% ;" @click="submitLogin"  v-loading.fullscreen.lock="fullscreenLoading"></el-button>
        </el-form>
      </div>
    </el-main>
  </el-container>
</template>

<script>

  export default {
    name: "Login",
    data(){
      return{
        loginForm:{
           username:'',
           password:'',
           mailCode:''
        },
        checked:true,
        rules: {
          username:[{required:true,message:'',trigger:'blur'}],
          password:[{required:true,message: '',trigger:'blur'}],
          // mailCode:[{required:true,message: '',trigger:'blur'}]
          mailCode:[{required:false,message: '',trigger:'blur'}]
        },
        fullscreenLoading:false,
        getCodeEnable:false,
        getCodeBtnText:'',
      }
    },
    methods:{
      submitLogin(){
        this.$refs.loginForm.validate((valid) => {
          if (valid) {
            this.fullscreenLoading=true;
            this.postKeyValueRequest('/admin/doLogin',this.loginForm).then(resp=>{
              setTimeout(()=>{
                this.fullscreenLoading=false;
              },1000);
              if (resp){
                //alert("");
                 //session
                 window.sessionStorage.setItem("admin",JSON.stringify(resp.obj));
                 this.$router.replace("/home");
              }
            })
          } else {
            this.$message.error("");
            return false;
          }
        });
      },
      gotoClientLogin(){
        this.$router.replace("/");
      },
      //
      getMailVerifyCode(){
        this.getRequest("/admin/mailVerifyCode").then(resp=>{
          if (resp){
            this.getCodeEnable=true;
            //30s
              let i=30;
            let id=setInterval(()=>{
              this.getCodeBtnText=i--+"s";
            },1000);
            setTimeout(()=>{
              clearInterval(id);
              this.getCodeEnable=false;
              this.getCodeBtnText="";
            },30000)
          }
        })
      }
    }
  }
</script>

